.Main-Container {
  justify-content: center;
}

.Container {
  /* background-color: red; */
  position: relative;
  display: block;
}
.InnerContainer {
  background-color: #f9f9fb;
  /* height: 30vh; */
  border-radius: 20px;
  margin: 0px 0px 0px 0px;
  text-align: center;
  height: auto;
}
.imageStyle {
  width: 60%;
  margin: 50px 0px 30px 0px;
}

#num {
  background-color: #fb9500;

  color: white;
  height: auto;
  padding: 10px 10px 10px 10px;
  border-radius: 10px !important;
  
}
#num p {
  color: white;
  display: inline;
}
.SecondInnerContainer {
  background-color: #f9f9fb;
  /* height: 55vh; */
  border-radius: 30px;
  margin: 20px 0px 0px 0px;
  justify-content: center;
  border-radius: 20px;
}
.handelButtonStyle {
  background-color: #fb9500;
  color: white;
  width: 50%;
}
.react-calendar__navigation {
  background-color: #fb9500 !important;
  color: white;
  font-weight: bold;
}
.react-calendar__month-view__weekdays {
  background: #62635d;
  color: white;
}
.react-calendar button {
  background: #999999;
}
.react-calendar {
  border: none !important;
}
